.single__course__item {
  padding: 15px;
}

.course__img img {
  border-radius: 10px;
  margin-bottom: 20px;
}

.enroll a {
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  color: #17bf9e;
}

.lesson,
.students,
.rating {
  font-size: 0.9rem;
  color: #0a2b1e;
  font-weight: 500;
}

.lesson i,
.students i,
.rating i {
  font-weight: 400;
}

.course__title {
  line-height: 2rem;
}

.course__top__left h2 {
  font-weight: 600;
}
.lr {
  display: flex;
  justify-content: space-between;
}

.cr {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 180px;
  /* border: 2px solid slategray; */
}

.re {
  display: flex;
  justify-content: space-between;
}

.cc {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ct {
  width: 50%;
  /* border: 1px solid; */
}

.abn {
  display: none;
  align-content: center;
  justify-content: center;
}

@media screen and (max-width: 766px) {
  .cb {
    display: none;
  }

  .ct {
    width: 100%;
    /* border: 1px solid; */
  }

  .cc {
    /* border: 1px solid; */
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    /* d-flex justify-content-between align-items-center */
  }

  .abn {
  display: flex;
  align-content: center;
  justify-content: center;
}
}
