.free__course__img {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.free__btn {
  position: absolute;
  bottom: -20px;
  right: 20px;
  /* z-index: -1; */
  padding: 4px 25px !important;
  font-size: 0.8rem;
}

.free__course__img img {
  border-radius: 10px;
}

.free__course__details h6 {
  font-weight: 600;
  line-height: 2rem;
}

.fc {
  display: flex;
  justify-content: space-between;
}

.fcc {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100px;
}